import { useState } from "react";
import style from "./AddContact.module.scss";
import { addContact } from "../../redux/contact/contact-opetations";
import { getContacts } from "../../redux/contact/contact-selectors";
import "@pnotify/core/dist/BrightTheme.css";
import { notice } from "@pnotify/core";
import "@pnotify/core/dist/PNotify.css";
import { v4 as uuidv4 } from "uuid";
import { useSelector, useDispatch } from "react-redux";

const FormAddContact = () => {
  const [userName, SetName] = useState("");
  const [number, SetNumber] = useState("");

  const contacts = useSelector(getContacts);
  const dispatch = useDispatch();

  const stateTransfer = (value) => dispatch(addContact(value));

  const alerError = () => {
    return contacts.items.find(({ name }) => {
      return name === userName;
    });
  };

  const submitformContact = (e) => {
    e.preventDefault();

    if (alerError()) {
      notice({ text: `${userName} is already in contacts` });
      return;
    }
    stateTransfer({ number, name: userName });

    SetName("");
    SetNumber("");
  };

  const getValueInput = (e) => {
    const { value, name } = e.currentTarget;

    switch (name) {
      case "name":
        SetName(value);
        break;
      case "number":
        SetNumber(value);
        break;

      default:
        console.log("error");
    }
  };

  return (
    <form
      onSubmit={submitformContact}
      htmlFor={uuidv4()}
      className={style.transparent}
    >
      <div className={style.formInner}>
        <label>
          Name
          <input
            onChange={getValueInput}
            type="text"
            value={userName}
            name="name"
            pattern="^[a-zA-Zа-яА-Я]+(([' -][a-zA-Zа-яА-Я ])?[a-zA-Zа-яА-Я]*)*$"
            title="Имя может состоять только из букв, апострофа, тире и пробелов. Например Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan и т. п."
            required
          />
        </label>
        <label>
          Number
          <input
            onChange={getValueInput}
            type="tel"
            value={number}
            name="number"
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="Номер телефона должен состоять цифр и может содержать пробелы, тире, круглые скобки и может начинаться с +"
            required
          />
        </label>
        <button className={style.button} type="submit">
          Add contact
        </button>
      </div>
    </form>
  );
};

export default FormAddContact;
